@import url("https://fonts.googleapis.com/css2?family=Lilita+One&family=Merriweather+Sans&family=Roboto:wght@700&family=Tilt+Neon&family=Tilt+Warp&display=swap");

body {
  position: relative;
  background-image: url("./images/back.jpg");
  background-size: cover;
  background-position: center center;
  background-repeat: no-repeat;
  background-attachment: scroll;
  color: white;
  font-family: sans-serif;
  height: 100vh;
}
.navbar ul li {
    list-style: none;
    font-family: "Merriweather Sans", sans-serif;
    text-shadow: 2px 2px 5px black;
}
.active {
  color: aqua;
}
li:hover a {
  cursor: pointer;
  color: aquamarine;  
}
.logo {
  font-size: 18px;
  cursor: pointer;
}
.navbar ul {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  font-size: 15px;
}
a {
  text-decoration: none;
  color: white;
}
h1 {
  width: 900px;
  text-align: center;
  font-size: 50px;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%,-50%);
  text-shadow: 2px 2px 5px black;
}
::selection{
  background-color: orange;
}
.container{
    border: 1px solid black;
    margin-top: 50px;
    width: 500px;
    height: 100px;
    margin-left: auto;
    margin-right: auto;
    text-align: center;
    box-shadow: 4px 4px 5px black;
    border-radius: 5px;
    transition: .5s;
    background-color: rgba(0, 0, 0, 1);
}
.skillset{
  border: 1px solid black;
  margin-top: 50px;
  width: 500px;
  height: 500px;
  margin-left: auto;
  margin-right: auto;
  text-align: center;
  box-shadow: -4px -4px 5px black;
  border-radius: 5px;
  transition: .5s;

}
.skill{
  text-align: center;
  padding: 10px;
  font-size: 30px;
  position: relative;
  color: black;
  font-weight: bolder;
}
.containertwo {
  width: 900px;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%,-50%);
  text-shadow: -2px -2px 5px white;
}
.wow{
  position: absolute;
 
  background: radial-gradient(circle at center, #12b398, #d0ff00);
  
  top: 0;
  left: 0;
  width: 0;
  height: 100%;
  content: "";
  transition: .5s;z-index: -1;
}
.skill:hover .wow{
  width: 100%;
}
.container:hover{
  transform: translateY(10px);
  /* transform: rotate(-1deg); */
  height: 400px;
    cursor: pointer;
  }
.container:hover .photu{
  display: block;
  height: 400px;
}
.container:hover h3{
color: wheat;
}
.container:hover .para{
  display: block;
height: 140px;
padding: 10px;
font-family: 'Times New Roman', Times, serif;
font-size: 20px;
}


.photu{
  height: 0;
  width: 400px;
  display: none;
    margin-left: auto;
    margin-right: auto;
    border-radius: 20px;
    transition: .5s;
    
  }
.para{
  background-color: white;
    color: black;
    border-radius: 5px;
    display: none;
    transition: .5s;

  }
  
  .socials{
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%,-50%);
    font-size: 30px;
    display: flex;
    flex-direction: column;
      border: 1px solid black;
      margin-top: 50px;
      width: 300px;
      height: 175px;
      margin-left: auto;
      margin-right: auto;
      text-align: center;
      box-shadow: 4px 4px 5px black;
      border-radius: 5px;
      transition: .5s;
      background-color: rgba(0, 0, 0, .5);
  }
  .SOCIAL{
    padding: 5px;
  }
  .SOCIAL:hover{
    background-color: red;
  }